		},
		mounted(){
			let This=this;
			var users=document.querySelectorAll('.adBook-user');
			for(let i=0;i<users.length;i++){
				users[i].onclick=function(){
					if(This.$refs.weHeader.isShow){
						return;
					}
					else{
						let username=this.children[1].innerHTML;
						let chatNum=this.getAttribute('wechatNum');
						let area=this.getAttribute('area');
						let avator=this.children[0].getAttribute('src');
						let realName=this.children[1].getAttribute('realName');
						let say=this.children[1].getAttribute('say');
						let chatTime=this.children[1].getAttribute('chatTime');
						let sex=this.children[1].getAttribute('sex');
						sessionStorage.setItem('nowChatNum',chatNum);
						if(area)
							sessionStorage.setItem('nowUserArea',area);
						if(say)
							sessionStorage.setItem('nowCuserSay',say);
						if(realName){
							sessionStorage.setItem('nowChatUser',realName);
							sessionStorage.setItem('nowChatNickname',username);
						}
						else{
							sessionStorage.setItem('nowChatUser',username);
						}
							
						if(chatTime)
							sessionStorage.setItem('nowChatTime',chatTime);
						sessionStorage.setItem('nowUserAvator',avator);
						sessionStorage.setItem('nowUserSex',sex);
						This.$router.push('userInfo');		
					}
				}
				
			}
		},
		activated() {
			this.closeHeaderPop();
			document.documentElement.scrollTop=document.body.scrollTop=this.addressTop; 
		},

		beforeRouteEnter (to, from, next) {
			if(to.name==='addressBook'){
				sessionStorage.removeItem('nowChatUser');
				sessionStorage.removeItem('nowUserAvator');
				sessionStorage.removeItem('nowUserArea');
				sessionStorage.removeItem('nowCuserSay');
				sessionStorage.removeItem('nowChatNickname');
				sessionStorage.removeItem('nowChatNum');
				sessionStorage.removeItem('nowChatTime');
				sessionStorage.removeItem('nowUserSex');
				next()
			}
			else{
				next();
			}
		},
		beforeRouteLeave (to, from, next) {
			this.addressTop=document.documentElement.scrollTop || document.body.scrollTop;
			if(this.$refs.weHeader.isShow ){
				this.closeHeaderPop();				 
				setTimeout(()=>{
				 	document.documentElement.scrollTop=document.body.scrollTop=this.addressTop;
				 	next(false);
				})				
					
		    }	
		    else{
		    	next();
		    }	
		    
		},
		components:{
			weHeader
		}
	}
</script>

<style scoped>
#addressBook{
	background: #fff;
}

.addressTop{
	margin-top: .9rem;
	background: #fff;
}
.addressTop li{
	margin:.1rem 0;
	margin-left:.3rem;
	padding-left: 0;
	display: flex;
	align-items: center;

}
.place{
	height: .86rem;
}
.addressTop li:last-child{
	border:none;
}
#addressBook >>> .van-index-bar__index {
	margin-bottom: .08rem;
	padding-right: 0.1rem;
}
#addressBook >>> .van-index-bar__sidebar {
	transform: translateY(-52%);
}
#addressBook >>> .van-index-bar{
	margin-bottom: 1.3rem;
}
.van-cell__title{
	font-size:.34rem;
}
.van-cell{
	padding: .2rem .26rem;
}
#addressBook >>> .van-index-anchor{
	background-color:#eee !important;
	color:#878787;
	padding-left: 0.34rem;
	line-height: 1.8;
}
.none{
	height: 0;
	opacity: 0;
}
.addressTop>li>span{
	display: inline-block; 
	background-repeat: no-repeat;
	background-size:60%;
	background-position: center;

}
.addressTop>li>span,.adBook-user>img{
	border-radius: 6px;
	width: .84rem;
	height: .84rem;
}
.addressTop>li>div{
	font-size:.38rem;
	width: 85%;
	margin-left: .26rem;
	padding: .3rem 0;
	border-bottom:1px solid rgba(233,233,233,.6);
}
.addressTop>li:last-child>div{
	border-bottom: 1px solid transparent;
}
.newFriend{
	background-color: #fb9f3c;
	background-image: url('~img/images/addressBook/newFriend.png');
}
.users{
	background-color: #06c261;
	background-image: url('~img/images/addressBook/users.png');
}
.label{
	background-color: #2781d7;
	background-image: url('~img/images/addressBook/label.png');
}
.publicNum{
	background-color: #2781d7;
	background-image: url('~img/images/addressBook/publicNum.png');
}
.adBook-user{
	font-size: .38rem;
	margin: .1rem 0 .1rem .26rem;
	display: flex;
	align-items: center;	
}
.adBook-user>p{
	margin-left: .26rem;
	display: inline-block;
	border-bottom:1px solid rgba(233,233,233,.6);
	padding: .3rem 0;
	width: 82%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.adBook-user:nth-last-of-type(1)>p{
	border-bottom: 1px solid transparent;
}
.adBook-user>img{
	vertical-align: middle;		
}
.van-index-bar__sidebar {
	margin-right: .1rem;
}
.userCount{
	font-size: .38rem;
	background: rgb(237,237,237);
	text-align: center;
	padding: .34rem 0;
	color: #999;
}
</style>


